import React from 'react'

function Controls(params) {
    let right = params.right
    if (params.right instanceof Array) {
        right = []
        right = params.right.map(node => {
            return <div className="level-item">{node}</div>
        })
    }
    return (
        <nav className="level is-mobile" style={params.style}>
            <div className="level-left">
                <div className="level-item">
                    {params.left}
                </div>
            </div>

            <div className="level-right">
                <div className="level-item">
                    {right}
                </div>
            </div>
        </nav>
    )
}

export default Controls